@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200;300;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Source Code Pro', monospace;
  font-weight: 500;
}
.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  background-color: rgb(197 228 232);
  align-items: center;
  justify-content: center;
}

.text-left{
  text-align: left;
}

.text-right{
  text-align: right;
}
.text-center{
  text-align: center;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.main-container{
    background-color: #f7f7f7;
    color: rgba(133, 127, 128);
    border-radius: 1rem;
    padding: 1.4rem;
    width: min(90vw, 480px);
    height: min(100%, 240px);
}
.main-container-row{
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.4em;
    gap: 1.4em;
}

.calc-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.tip-select-grid{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    gap: 10px;
}

.form-input-row{
    width:100%;
    display: block;
}

.total-container{
    background-color: rgba(27,70,77);
    border-radius: 1rem;
    color: #fff;
    padding: 1.4em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
}



@media (max-width:768px) {
    .main-container{
        height: min(100%, 480px);
    }
    .main-container-row{
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 1.4em;
        gap: 1.4em;
    }
    
}


.subtotal-content{
    display: flex;
    justify-content: space-between;
    padding: .7rem 0px;
}

.subtotal-content label{
    font-size: .7rem;
}

.subtotal-content label > em{
    display: block;
    font-size: .6rem;
    color: rgb(75,134,137);
}

.subtotal-content .subtotal-amount{
    font-size: 2rem;
    color: rgb(44,192,173);
    font-weight: 600;
    line-height: 1.5rem;
}



.form-control{
    width: 100%;
    display: block;
    background-color: rgb(225, 235, 240);
    border: none;
    color: rgba(27,70,77);
    line-height: 1.6em;
    border-radius: .3em;
    font-size: .95em;
    font-weight: 600;
    font-family: 'Source Code Pro', monospace;
    transition: ease-in-out .5s;
    padding: 5px;
    box-sizing: border-box;
}

.form-control:focus{
    outline:none;
    background-color: rgb(215, 225, 230);
    box-shadow: 0px 0px 4px rgb(200, 215, 220) inset;
}
.form-control::-webkit-input-placeholder{
    color:rgb(159,184,184);
}
.form-control:-ms-input-placeholder{
    color:rgb(159,184,184);
}
.form-control::placeholder{
    color:rgb(159,184,184);
}
button.form-control{
    background-color: rgb(206, 217, 223);
    color: rgba(27,70,77);
}
button.form-control:focus, button.form-control.active{
    background-color: rgba(27,70,77);
    color: rgb(44,192,173);
}
button.form-control:disabled{
    opacity: .25;
}

.form-input-row > label{
    display: block;
    font-size: .7rem;
    font-weight: 600;
    line-height: 1.4rem;
}

.form-icon .form-control{
    padding-left: 30px;
}

.form-icon .icon {
    position: absolute;
    font-size: .7em;
    color:rgb(159,184,184);
    padding: 8px;
    height: 1.6em;
}

